* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    outline: none;
}

body {
    font-family: 'Fira Sans', sans-serif;
    background-color: #6ccfff;

    background: rgb(48,201,254);
    background: -webkit-linear-gradient(45deg, rgba(48,201,254,1) 28%, rgba(147,228,255,1) 68%);
    background: linear-gradient(45deg, rgba(48,201,254,1) 28%, rgba(147,228,255,1) 68%);
    background-repeat: no-repeat;
    background-attachment:fixed;
}

#root {
    height: 100vh;
}

