.create-round-wrapper {
    margin-bottom: 15px;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}

.create-round__input:nth-child(1) {
    background-color: #bababa;
    color: #2b2b2b;
}
.create-round__input {
    background-color: #2b2b2b;
    border: none;
    border-radius: 100px;
    padding: 10px;
    color: whitesmoke;
    margin-right: 5px;
    margin-bottom: 5px;
}

.create-round__add-btn {
    width: 35px;
    height: 35px;
    color: whitesmoke;
    background-color: #2b2b2b;
    border: none;
    border-radius: 50px;
    font-size: 20px;
    cursor: pointer;
    box-shadow: 0 3px 0 #202020;
    transform: translateY(-3px);
}
.create-round__add-round-btn {
    background-color: #bababa;
    color: #2b2b2b;
    box-shadow: 0 3px 0 #868686;
}
.create-round__add-btn:hover {
    box-shadow: 0 2px 0 #202020;
    transform: translateY(-2px);
}
.create-round__add-round-btn:hover {
    box-shadow: 0 2px 0 #868686;
}
.create-round__add-btn:focus {
    background-color: #66af50;
    box-shadow: 0 3px 0 #478342;
}

.create-round__delete-btn {
    background-color: transparent;
    border: none;
    width: 13px;
    height: 13px;
    display: flex;
    justify-content: center;
    align-content: center;
    margin-bottom: 5px;
    margin-right:10px ;
    cursor: pointer;
    transition: translateY, 0.1s;
}
.create-round__delete-btn:hover {
    transform: translateY(-2px);
}
.create-round__delete-btn img {
    width: 100%;
    height: 100%;
}


.round_answer-right {
    background-color: #9cffc8;
    color: #202020;
}

.answer-select_block {
    margin: 7px;
    margin-left: 25px;
}

.answer-select_block input[type=radio] {
    margin-right: 5px;
}

.answer-select_block label {
    color: whitesmoke;
    font-size: 20px;
}
